import React from "react";
// import "./header.css";
import { FaFacebookF, FaPinterestP, FaPhone } from "react-icons/fa";
import { IoLogoTwitter, IoLogoInstagram, IoLogoLinkedin,
         IoLogoYoutube, IoLogoGithub, IoMdMail } from "react-icons/io"


function ContactNav () {

    return (
        <div className="contact-nav">
            <SocialIcons class="social-icons" />
            <ContactMail class="contact-icons" />
        </div>
    );
}

function SocialIcons (props) {
    return (
        <div>
            <a href="#"><FaFacebookF className={props.class} /></a>
            <a href="#"><FaPinterestP className={props.class} /></a>
            <a href="#"><IoLogoTwitter className={props.class} /></a>
            <a href="#"><IoLogoInstagram className={props.class} /></a>
            <a href="#"><IoLogoLinkedin className={props.class} /></a>
            <a href="#"><IoLogoYoutube className={props.class} /></a>
            <a href="#"><IoLogoGithub className={props.class} /></a>
        </div>
    )
}

function Info (props) {
    return (
        <h5>
            Free shipping for standard order over $100
        </h5>
    )
}

function ContactMail (props) {
    return (
        <div className="contact-mail">
            <span><FaPhone className={props.class} /> (+234) 903-4726-175</span>
            <span><IoMdMail className={props.class} /> tracemall@tracetechie.com</span>
        </div>
    )
}

function MailAndCurrency (props) {
    return (
        <div>
            <h5>tracemall@tracetechie.com</h5>
        </div>
    )
}

export default ContactNav;