import React from "react";
// import "./header.css";
import { IoMdPersonAdd, IoIosLogIn} from "react-icons/io";
import { MdLibraryAdd } from "react-icons/md";


class Nav extends React.Component {

    state = {
        navLinks: this.props.navLinks
    }

    listLinks = () => {
        const links = this.state.navLinks.map((link) => {
            return <li><a href={`#${link}`}>{link}</a></li>;
        });
        return <ul className="ul">{links}</ul>
    }

    render () {

        return (
            <nav className="nav">
                <Logo class="logo" />
                {this.listLinks()}
                <Icons register="register" login="login" advert="advert" class="icons" />
            </nav>
        )
    }
}

function Icons (props) {
    return (
        <div className="right-icons-container">
            <IoIosLogIn className={props.login, props.class} />
            <IoMdPersonAdd className={props.register, props.class} />
            <MdLibraryAdd className={props.advert, props.class} />
        </div>
    )
}

function Logo (props) {
    return (
        <h2 className={props.class}>TraceMall</h2>
    )
}

export default Nav;