import React from "react";
import Nav from "./header/nav.js";
import ContactNav from "./header/contact-nav.js";

class Header extends React.Component {
    render () {
        const links = ["Home", "Shop", "Blog", "About", "Contact"];
        
        return (
            <div>
                <ContactNav />
                <Nav navLinks={ links }/>
            </div>
        )
    }
}

export default Header;