import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import Select from "react-select";
import makeAnimated from 'react-select/animated';

import slide1 from "../../images/post-1.jpg";
import slide2 from "../../images/post-2.jpg";
import slide3 from "../../images/post-3.jpg";
import { SignalCellularNullTwoTone } from "@material-ui/icons";

const animatedComponents = makeAnimated();

function BannerInfo () {
    return (
        <div className="banner-info">
            <h1>We Connect You with your client</h1>
            <h4>Render a service / Sell Something or Buy From Someone </h4>
        </div>
    )
}

function SliderOverlay (props) {
    const dropList = [
        {value: "abia", label: "Abia"},
        {value: "adamawa", label: "Adamawa"},
        {value: "akwa ibom", label: "Akwa ibon"},
        {value: "anambra", label: "Ananmbra"},
        {value: "bauchi", label: "Bauchi"},
        {value: "bayelsa", label: "Bayelsa"},
        {value: "benue", label: "Benue"},
        {value: "borno", label: "Borno"},
        {value: "cross river", label: "Cross River"},
        {value: "delta", label: "Delta"},
        {value: "edo", label: "Edo"},
        {value: "enugu", label: "Enugu"},
        {value: "gombe", label: "Gombe"},
        {value: "jigawa", label: "Jigawa"},
        {value: "kaduna", label: "Kaduna"},
        {value: "kwara", label: "Kwara"}
    ];

    // user the variables below when you need to get the values of the select component
    const [ region, setRegion ] = React.useState("abia");
    const [ category, setCategory ] = React.useState("abia");

    const changeRegion = (obj) => {
        setRegion(obj.value);
    }

    const changeCategory = (obj) => {
        setCategory(obj.value);
    }

    return (
        <div className="slider-overlay">
            <BannerInfo />
            <div className="overlay-container">
                <div className="select-container">
                    <h4>Region: </h4>
                    <Select className="select"
                            components={animatedComponents}
                            closeMenuOnSelect={true}
                            value={dropList.find(item => item.value === region)}
                            options={dropList} 
                            onChange={changeRegion}
                        />
                </div>
                <div className="select-container">
                    <h4>Category: </h4>
                    <Select className="select"
                        components={animatedComponents}
                        closeMenuOnSelect={true}
                        value={dropList.find(item => item.value === category)}
                        options={dropList} 
                        onChange={changeCategory}
                    />
                </div>
            </div>
        </div>
    )
}

function Slide (props) {
    return (
        <div className="slide" style={{backgroundImage: props.src}}>
            {/* this is the slide component */}
        </div>
    )
}

function BannerSlider (props){

    const state = [
        {image: slide1},
        {image: slide2},
        {image: slide3}
    ]

    const settings = {
        dots: false,
        infinite: true,
        speed: 1000,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 4000
    };

    const ShowSlides = () => {
        const slide = state.map(item => {
            return <Slide src={`url(${item.image})`} />;
        })
        return slide;
    }

    return (
        <div className="banner-area">
            <SliderOverlay />
            <Slider className="slider" {...settings}>
                {ShowSlides()}
            </Slider>
        </div>
    )
    
}


export default BannerSlider;